.tag {
  font-size: 12px;
  padding: 4px 8px;
  background: var(--font-highlight-color);
  color: white;
  border: 1px solid var(--font-highlight-color);
  border-radius: 8px;
  margin: 4px 5px;
  display: inline-block;
}

.tagHover {
  font-size: 12px;
  padding: 4px 8px;
  background: white;
  color: var(--font-highlight-color);
  border: 1px solid var(--font-highlight-color);
  border-radius: 8px;
  margin: 4px 5px;
  display: inline-block;
  cursor: default;
}